body {
  margin: 0;
  font-size: 30px;
}
$width-each-block: 100px;
$height-each-block: 100px;
$days: "calendar-mon" 0 "calendar-28" 28, "calendar-tue" 1 "calendar-29" 29, "calendar-wed" 2 "calendar-30" 30, "calendar-thu" 3 "calendar-31" 31, "calendar-fri" 4 'none' 0, "calendar-sat" 5 'none' 0, "calendar-sun" 6 'none' 0;


.calendar {
  display: flex;
  width: 800px;
  flex-wrap: wrap;
  margin: auto;
  position: absolute;
  top: calc(50% - 252px); // что бы было по центру
  left: calc(50% - 353.5px); // что бы было по центру
}

@each $day, $count, $date, $number in $days {
  .#{$date} :nth-child(-n+#{$number}) {
    width: $width-each-block;
    height: $height-each-block;
    border: 1px solid black;
    box-sizing: border-box;
    background-color: #eee;
    margin-right: 1px;
    text-align: center;
    line-height: $height-each-block;
    transition: all 0.5s ease-in-out;
  }
  .#{$day} :first-child {
    margin-left: $count*(100 +1px);
  }

  @for $i from 1 through $number {
    .#{$date} :nth-child(#{$i})::before {
      content: "#{$i}" ;
    }
  }
}

.calendar :nth-child(n):hover {
  background-color: pink;
  transform: translateY(-20px);
  cursor: pointer;
}
